@import "mixins";


@media all  and (max-width: 1530px) {

	.skoda_header_dropdown:active,
	.skoda_header_dropdown:visited,
	.skoda_header_dropdown:focus,
	.skoda_header_dropdown:hover {
			text-decoration: none !important;
			color: #000; }
	.skoda_header_dropdown {
		display: block;
		// a:active,
		// a:visited,
		// a:focus,
		// a:hover
		// a
		// 	text-decoration: none
		.menu_arrow_down {
			display: inline-block;
			width: 10px;
			height: 10px;
			margin-left: 10px;
			border-bottom: 1px solid $main-green;
			border-left: 1px solid $main-green;
			@include transform(rotate(-45deg) translateY(-5px)); } }
	.skoda_header {
		.skoda_header_nav {
			.dropdown_li {
				position: static; }
			.dropdown_menu {
				width: 96%;
				left: 2%;
				border-radius: 0;
				box-shadow: none;
				border: none;
				margin: 0;
				padding: 0;
				.skoda_header_nav_list-item {
					display: block;
					border-top: 1px solid $main-grey;
					// border-bottom: 1px solid $main-grey
					a {
						text-align: left; } } } } } }

@media all  and (max-width: 1070px) {
	.skoda_header {
		.skoda_header_location {
			display: none; } } }

@media all  and (max-width: 999px) {
	.skoda_header {
		.skoda_models_dropdown_menu {
			width: 100%;
			left: 0%;
			.skoda_models_dropdown_menu_li {
				width: 33.333%; } }

		.skoda_header_nav {
			.dropdown_menu {
				width: 100%;
				left: 0; } }
		.skoda_header_tools {
			.tools_dropdown_menu {
				width: 100%;
				left: 0;
				.tools_dropdown_menu_li {
					width: 45%;
					max-width: 330px; } } } }
	.skoda_jumbotron_section {
		margin-bottom: 170px;
		.skoda_jumbotron_link {
			.skoda_jumbotron_link_pic {
				background-size: cover; } }
		.skoda_jumbotron_button {
			width: 100%;
			bottom: -130px;
			left: 50%;
			padding: 0;
			text-align: center;
			@include transform(translateX(-50%));
			.skoda_jumbotron_h {
				color: #000; }
			.skoda_jumbotron_btn {
				padding-left: 30px;
				padding-right: 30px; } } } }


@media all  and (max-width: 885px) {
	.skoda_header {
		.skoda_header_helm {
			display: none; } } }

@media all  and (max-width: 700px) {
	.skoda_header {
		.skoda_header_book {
			display: none; } } }
@media all  and (max-width: 816px) {

	.bottom_menu {
		.bottom_menu_dropdown_button {
			display: block; }
		.bottom_menu_dropdown {
			@include box-shadow(none);
			background: none;
			border: none;
			border-radius: 0;
			float: none; }
		.bottom_menu_column:last-child {
			display: none; } }
	.bottom_menu_column_new {
		display: block;
		letter-spacing: normal; } }


@media all  and (max-width: 992px) {
	.bottom_menu {
		.bottom_menu_container {
  	  width: auto; } } }
@media all  and (max-width: 768px) {
	.bottom_menu {
		.bottom_menu_container {
  	  width: auto; } } }

@media all  and (max-width: 659px) {
	.skoda_header_dropdown {
		border-left: 1px solid $main-grey;
		padding: 13px 12px;
		.dropdown_capt,
		.menu_arrow_down {
			display: none; }
		.mob_button {
			display: inline-block;

			@include transform(translateY(5px));
			.mob_button_bar {
				background: $main-green;
				width: 20px;
				height: 2px;
				margin: 0px 0px 4px;
				display: block; } } }



	.skoda_header {
		height: 50px;
		.skoda_models_dropdown_menu {
			.skoda_models_dropdown_menu_h {
				font-size: 24px; }
			.skoda_models_dropdown_menu_li {
				width: 50%; } }
		.skoda_header_account {
			.skoda_header_account_button {
				padding: 10px 12px;
				font-size: 22px; } }
		.skoda_logo {
			padding: 2px; }
		.skoda_header_nav {
			// position: absolute
			// top: 15px
			// right: 50px
			.skoda_header_nav_list-item {
				a {
					padding-top: 17px;
					padding-bottom: 16px; } } }
		.skoda_header_tools {
			display: none; }
		.skoda_header_nav {
			float: right;
			// margin-top: 10px

			.skoda_header_nav_list-item {
				.skoda_header_nav_hide_list {
					display: block; } } } }

	.skoda_jumbotron_section {
		margin-bottom: 140px;
		.skoda_jumbotron_button {
			bottom: -100px;
			.skoda_jumbotron_h {
				font-size: 24px;
				margin-top: 0; } } }
	.skoda_text_section {
		.skoda_text_section_h {
			font-size: 24px; } } }

@media all  and (max-width: 475px) {
	.skoda_tools_section {
		.skoda_tools_item {
			width: 45%;
			.skoda_tools_item_img {
				margin-top: 20px; }
			.skoda_tools_item_link {
				margin-bottom: 20px; } } }
	.bottom_menu {
		.bottom_menu_column {
			width: 45%; }
		.bottom_menu_column:nth-child(3) {
			width: 95%;
			.bottom_menu_column_ul {
				.bottom_menu_column_li {
					display: inline-block;
					margin-right: 5px; } } } } }
@media all  and (max-width: 425px) {
	.skoda_header {
		.skoda_logo {
			margin-left: 0px;
			vertical-align: middle;
			padding: 0; } }
	.skoda_models {
		.model_capt {
			display: none; }
		.model_capt_hide {
			display: inline-block;
			color: $main-green; } }
	.skoda_tools_section {
		.skoda_tools_item {
			width: 100%;
			.skoda_tools_item_link {
				margin-top: 0px; } } } }
